
  /* GLOBAL LAYOUT */
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: black;
  }

  body {
    background-color: rgb(236, 193, 0);
    font-family: 'Source Code Pro', monospace;

  }

  h1, h2, h3 , h4, h5 , h6 {
    text-transform: lowercase;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    line-height: 1.8em;
    letter-spacing: .2em;
  }


.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px;
  }

  .logo {
      font-size: 30px;
      text-transform: uppercase;
      font-weight: 900;
  }
  
a{
      text-decoration: none;
  }
  .navLinks a {
    padding: 20px;
    text-transform: lowercase;
  }

  footer {
      margin: 30px;
    display: flex;
    justify-content: space-evenly;
  }
  
  /* ABOUT */

  .container {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 30px;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .aboutContainer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

/* PROJECTS */

.allCards{
  display: flex;
  flex-direction: column;
}

.projectCard {
  border: 6px dashed rgb(204, 167, 1);
  margin: 20px;
  padding: 40px;
}

.hr {
  border: 1px dashed rgb(204, 167, 1);
  margin-top: 15px;
  margin-bottom:15px;
}
.pLinks {
  display: flex;
  justify-content: space-between;
}

.projectEmoji {
  font-size: 30px;
}